


import XHeader from 'vux/src/components/x-header/index.vue'

import { register, yzmGet } from 'src/service/api'
import {setStore,getStore} from 'src/config/mUtils'

export default {

components: {XHeader},
data() {

return {
borderColor: {
borderColor: '#333'
},
yzmShow:true,
countdown:'',
mobile:'',
code:'',
mobile:'',
password:'',
password2:'',
agreeShow:true,
};
},

computed: {},

watch: {},

methods: {
back(){
this.$router.go(-1);
},
toLogin(){
this.$router.push('/login');
},
agreement(){
this.agreeShow = !this.agreeShow
},
yzm(){
let mobile = this.mobile;
if(mobile == ''){
this.$vux.toast.show({
text: '',
type:'text',
position: 'middle'
})
return
}
console.log(mobile);
yzmGet({mobile:mobile,type:'register'}).then(res=>{
console.log(res);
if(res.code == 1){
this.setTime();
}else if(res.code == 0){
this.$vux.toast.show({
text: res.msg,
type:'text',
position: 'middle'
})
}
});
},
setTime(){
this.yzmShow = false;
this.countdown = 60;
let auth_timer = setInterval(()=>{
this.countdown --;
if(this.countdown <= 0 ){
this.yzmShow = true;
clearInterval(auth_timer);
}
},1000)
},
register(){
let password = this.password;
let password2 = this.password2;
let mobile = this.mobile;
let code = this.code;
if(password != password2){
this.$vux.toast.show({
text: "",
type:'text',
position: 'middle'
})
return
}
if(password.length<6){
this.$vux.toast.show({
text: "6",
type:'text',
position: 'middle'
})
return
}
register({username:mobile,code:code,password:password}).then(res=>{
console.log(res);
if(res.code == 1){
this.$vux.toast.show({
text: "",
type:'text',
position: 'middle'
})
setStore("user",this.mobile);
setTimeout(()=>{
this.$router.push("/personFile");
},1000)
}else {
this.$vux.toast.show({
text: res.msg,
type:'text',
position: 'middle'
})
}
})
}
},

created() {

},

mounted() {

},
beforeCreate() {}, // - 
beforeMount() {}, // - 
beforeUpdate() {}, // - 
updated() {}, // - 
beforeDestroy() {}, // - 
destroyed() {}, // - 
activated() {}, //keep-alive
}



